.board-container {
  display: flex;
  gap: 20px;
  padding: 20px;
}

.column {
  padding: 10px;
  background: #f4f4f4;
  min-width: 200px;
  border-radius: 5px;
  flex: 1;
}

.todo-item {
  background: white;
  padding: 8px;
  margin: 5px 0;
  border-radius: 4px;
  cursor: grab;
}